import React from "react";
import logo from "../../logo.svg";
import style from "./styles/Start.module.css";
import { useNavigate } from "react-router-dom";
import AnimatedPage from "../../AnimatedPage";

const Start = () => {
  const navigate = useNavigate();

  const navigateToProfile = () => {
    navigate("/Profile");
  };

  return (
    <div className={`${style.Start} col-lg-10 col-12`}>
      <AnimatedPage>
        <h1 className={style.Start__h1}>
           ,    React  Kenshi
        </h1>

        <div className={style.Start__logoContainer}>
          <img src={logo} alt="React logo" />

          <h2 className={style.Start__h2}>
            React  JavaScript-     
              . React  
             Facebook, Instagram   
              . React   
                .
          </h2>
        </div>
        <div className="d-flex justify-content-center">
          <button
            className={`${style.Start__button} btn btn-dark`}
            onClick={navigateToProfile}
          >
             
          </button> 
        </div>
      </AnimatedPage>
    </div>
  );
};

export default Start;
